.parallax-element img {
  width: 100%;
}

.parallax-container {
  position: relative;
  height: 80vw;
  z-index: 1;
}

.parallax-container-2 {
  position: relative;
  width: 100%;
  height: 100%;
}

#element-1 {
  position: absolute;
  bottom: 0%;
  left: -5%;
  right: 0%;
  height: 120%;
  width: 110%;
}

#element-2 {
  position: absolute;
  bottom: 0%;
  left: 0%;
  height: 100%;
  width: 100%;
  z-index: 10;
}

#element-3 {
  position: absolute;
  bottom: 0%;
  left: 40%;
  right: 0%;
  height: 65%;
  width: 35%;
  z-index: 10;
}

#element-4 {
  position: absolute;
  bottom: 0%;
  left: 32%;
  right: 0%;
  height: 93%;
  width: 40%;
}

#element-5 {
  position: absolute;
  bottom: 0;
  left: 0%;
  right: 0%;
  height: 54%;
  width: 100vw;
  z-index: 10;
  img {
    position: absolute;
    bottom: 0;
    width: 190%;
  }
}
